<template>
  <div class="row">
    <div class="col-4 mb-4" v-for="column in columnList" :key="column.id">
      <div class="card h-100 shadom-sm">
        <div class="card-body text-center">
          <img class="round-circle border border-light w-25 my-3" :src="column.avatar" :alt="column.avatar">
          <h5 class="card-title">{{column.title}}</h5>
          <p class="card-text text-left">{{column.description}}</p>
          <router-link :to="`/column/${column.id}`" class="btn btn-outline-primary">进入专栏</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
import { computed, defineComponent, PropType } from 'vue'
export interface ColumnProps {
  id: number,
  title: string,
  avatar?: string,
  description: string
}
export default defineComponent({
  name: 'CloumnList',
  props: {
    list: {
      type: Array as PropType<ColumnProps[]>,
      require: true
    }
  },
  setup(props) {
    const columnList = computed(() => {
      if (!props.list) return []
      return props.list.map(column => {
        if (!column.avatar) {
          // column.avatar = require('@/assets/logo.png')
          column.avatar = 'https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fpic3.zhimg.com%2F50%2Fv2-2c739e302fd6ed9b6c6d13b4c3e58b7b_hd.jpg&refer=http%3A%2F%2Fpic3.zhimg.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1634297584&t=cdfc5af458289d3caf60d9e07d9eaf36?x-oss-process=image/resize,h_100,m_lfit'
        }
        return column
      })
    })
    return {
      columnList
    }
  }
})
</script>
