<template>
  <div>
    <test-second></test-second>
  </div>
</template>

<script lang="ts">
import { defineComponent, provide, reactive } from 'vue'
import TestSecond from '../components/test2.vue'
export default defineComponent({
  props: {
  },
  components: {
    TestSecond
  },
  setup() {
    const o:any = reactive({
      name:'bobo',
      age:22
    })
    const updateItem = (key:string,val:any) => {
      o[key] = val
    }
    provide('userinfo',{
      val:o,
      updateItem
    })
    return {
      o,
      updateItem
    }
  }
})
</script>

