
import { TableProps } from '@/testData'
import { computed, defineComponent, PropType } from 'vue'
export default defineComponent({
  name: '',
  props: {
    userList: {
      type: Array as PropType<TableProps[]>,
      require: true
    }
  },
  setup(props) {
    const list = computed(() => {
      if (!props.userList) return []
      return props.userList.filter(user => {
        return user.name.length > 2
      })
    })
    return {
      list
    }
  }
})
