import { useStore } from 'vuex'
export default defineComponent({
  components: {
    ColumnList,
    Loader,
    UserList
  },
  name: '',
  props: {

  },
  setup() {
    const isLoading = ref(true)
    setTimeout(() => {
      isLoading.value = false
    },2000)
    onMounted(() => {
      store.dispatch('testAction')
