import axios from 'axios';
import { createStore } from 'vuex'
import { testData, postData, ColumnProps, PostProps, TableProps, userList } from './testData';
interface UserProps {
  isLogin: boolean,
  name?: string,
  id?: number,
  columnId?: number
}
export interface GlobalDataProps {
  columns: ColumnProps[],
  posts: PostProps[],
  user: UserProps,
  userList: TableProps[]
}
const store = createStore<GlobalDataProps>({
  state: {
    columns: testData,
    posts: postData,
    user: { isLogin: true, name: 'bobozhuang', columnId: 1 },
    userList: userList
  },
  // mutations 相当于vue的methods
  mutations: {
    login(state) {
      state.user = { ...state.user, isLogin: true, name: 'bobozhuang'}
    },
    logout(state) {
      state.user = { ...state.user, isLogin: false, name: ''}
    },
    fetchCloumns(state, rawData) {
      state.columns = rawData.data.list
    },
    testAction(state, rawData) {
      state.user.name = rawData.data.name
    },
  },
  actions: {
    fetchCloumns(context) {
      axios.get('/columns').then((res) => {
        context.commit('fetchCloumns', res)
      })
    },
    testAction({commit}) {
      commit('testAction', {data:{name:'bb'}})
    }
  },
  // getters 相当于vue的computed
  getters: {
    getBiggerColumnLen(state) {
      return state.columns.filter(c => c.id > 2).length
    },
    // getter可以是返回一个方法， 也可以返回一个变量  想当vuex的computed
    getColumnById: (state) => (id:number) => {
      return state.columns.find(c => c.id === id)
    },
    getPostsByCid: (state) => (cid:number) => {
      return state.posts.filter(post => post.columnId === cid)
    }
  }
})
export default store