<template>
  <div class="login-page">
    <validate-form @form-submit="onFormSubmit">
      <div class="mb-3">
        <label for="exampleInputEmail1" class="form-label">Email address</label>
        <validate-input ref="inputRef" type="text" v-model="emailVal" placeholder="请输入邮箱地址" :rules="emailRules"></validate-input>
      </div>
      <div class="mb-3">
        <label for="exampleInputPassword1" class="form-label">Password</label>
        <validate-input type="password" v-model="pwdVal" placeholder="请输入密码" :rules="pwdRules"></validate-input>
      </div>
      <div class="mb-3 form-check">
        <input type="checkbox" class="form-check-input" id="exampleCheck1">
        <label class="form-check-label" for="exampleCheck1">Check me out</label>
      </div>
      <template #submit>
        <span class="btn btn-primary">提交</span>
      </template>
  </validate-form>
  </div>
</template>

<script lang="ts">
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import { useRouter } from 'vue-router'
import ValidateForm from '../components/ValidateForm.vue'
import 'bootstrap/dist/css/bootstrap.min.css'
import { defineComponent, reactive, ref } from 'vue'
import { useStore } from 'vuex'
export default defineComponent({
  name: '',
  props: {

  },
  components: {
    ValidateInput,
    ValidateForm
  },
  setup() {
    const store = useStore()
    const router = useRouter()
    const inputRef = ref<any>()
    const emailVal = ref('')
    const pwdVal = ref('')
    const emailRules:RulesProp = [
      { type: 'required', message: '电子邮箱地址不能为空' },
      { type: 'email', message: '电子邮箱地址格式错误' }
    ]
    const pwdRules = [
      { type: 'required', message: '密码不能为空' }
    ]
    const emailRef = reactive({
      val: '',
      error: false,
      message: ''
    })
    const validateEmail = () => {
      const reg = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,5}$/
      if (emailRef.val.trim() === '') {
        emailRef.error = true
        emailRef.message = 'can not be empty'
      } else if (!reg.test(emailRef.val)) {
        emailRef.error = true
        emailRef.message = 'should be validate email'
      } else {
        emailRef.error = false
      }
    }
    const onFormSubmit = (res:boolean) => {
      console.log('inputRef', emailVal.value)
      if (emailVal.value && pwdVal.value) {
        router.push('/')
        store.commit('login')
      }
    }
    return {
      emailRef,
      validateEmail,
      emailRules,
      pwdRules,
      emailVal,
      pwdVal,
      onFormSubmit,
      inputRef,
      router
    }
  }
})
</script>

